.register-card {
  width: 300px;
  height: 500px;
  position: relative;
}

.welcome-msg {
  margin-top: 33px;
  line-height: 0.2;
  text-align: center;
}

.welcome-msg img {
  width: 100px;
}

.input-container {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
}

.cloud-left {
  position: absolute;
  top: calc(100% - 65px);
  z-index: 1;
}

.cloud-right {
  position: absolute;
  right: 0;
  top: calc(100% - 45px);
  z-index: 1;
}
.register-btn {
  width: 100%;
  margin: 11px;
  z-index: 100;
}

.input {
  width: calc(100% - 25px) !important;
}


